import React, {Component} from 'react'
import axios from 'axios'

class Tech extends Component {
    state = {
        posts: []
    }

    componentDidMount() {
        axios.get("https://newsapi.org/v2/top-headlines?sources=techcrunch,techradar,recode,hacker-news&apiKey=c7e164ceba2b427fa40d2873438d4627")
            .then(res => {
                this.setState ({
                    posts: res.data.articles
                })
                console.log(res.data)
            })
    }

    render() {
        const { posts } = this.state;
        const postList = posts.length ? (
            posts.map(post => {
                return (
                    <div className="post card" key={Math.random()}>
                        <div className="card-content">
                        
                            <span className="card-title"> {post.title} </span>
                            <p> {post.content} </p>
                            <button><a href={post.url}> View Post </a></button>
                            <p> Source: {post.source.name} </p>
                        </div>
                    </div>
                )
            })
        ) : (
            <h4> Loading Tech news...</h4>
        )

        return (
            <div>
                {postList}
            </div>
        )
    }
}

export default Tech